import vue from "vue";
import CustToast from "./Toast.vue";

const ToastConstructor = vue.extend(CustToast);

// 定义弹出组件的函数 接收三个参数 消息 toast类型 显示时间
function showToast(message, type = "info", duration = 2000, wrap) {
  // 实例化一个 CustToast.vue
  const _toast = new ToastConstructor({
    data() {
      return {
        showToast: true,
        type: type,
        message: message,
        duration: duration
      };
    }
  });

  let element = _toast.$mount().$el;
  if (wrap) {
    wrap.appendChild(element);
  } else {
    document.body.appendChild(element);
  }

  setTimeout(() => {
    _toast.showToast = false;
  }, duration);
}

showToast.install = Vue => {
  Vue.prototype.$toast = showToast;
};

export default showToast;
