<template>
  <div class="CustToast" :class="type" v-if="showToast">{{ message }}</div>
</template>

<script>
export default {
  /**
   * 自己封装的Toast v0.2
   * params: showToast Boolean 是否激活toast 默认 false
   * params: type String       toast提示类型 共info success，fail，warning 四个选项 默认info
   * params: message String    toast消息
   * params: duration Number      toast显示时间 默认 3000ms
   * */
  name: "CustToast",
  data() {
    return {
      showToast: true,
      type: "info",
      message: "消息提示",
      duration: 3000
    };
  }
};
</script>

<style scoped>
.CustToast {
  min-width: 420px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #ebeef5;
  font-size: 14px; /* no */
  position: fixed;
  left: 50%;
  top: 20px;
  transform: translateX(-50%);
  background-color: #edf2fc;
  transition: opacity 3s linear, transform 3s, top 2s;
  overflow: hidden;
  padding: 15px 15px 15px 20px; /* no */
  display: flex;
  align-items: center;
  z-index: 2000002;
}

@keyframes show-toast {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.success {
  color: #67c23a;
  background: rgb(225, 243, 216);
  border: #e1f3d8;
}

.warning {
  color: #e6a23c;
  background: rgb(250, 236, 216);
}

.error {
  color: #f56c6c;
  background: rgb(253, 226, 226);
  border: #fde2e2;
}
</style>