import axios from "axios";
import Toast from "./toast";
import loading from "@/common/loading/loading";

const callTimeout = 120000;

let indicatorCount = 0;
let loadingInstance = null;

function createService(type, timeout, isClose) {
  const service = axios.create({
    baseURL: process.env.VUE_APP_BASE_API,
    timeout: timeout
    // transformResponse: [
    //   function (data) {
    //     return JSONbigString.parse(data);
    //   }
    // ]
  });

  // request拦截器
  service.interceptors.request.use(
    config => {
      config.headers["Cache-Control"] = "no-cache";

      // const token = session.getToken();
      // if (token) {
      //   config.headers["X-IDP-Token"] = token;
      // }

      if (type === 3) {
        if (indicatorCount === 0) {
          console.log(loading);
          loadingInstance = loading({
            color: "#00a8ff",
            iconfont: "icon-redupaixu"
          });
        }
        ++indicatorCount;
      }
      return config;
    },
    error => {
      if (type === 3) {
        indicatorCount = 0;
        if (loadingInstance != null) {
          loadingInstance.close();
          loadingInstance = null;
        }
      }
      console.log("req error: " + JSON.stringify(error));
      Promise.reject(error);
    }
  );

  // respone拦截器
  service.interceptors.response.use(
    response => {
      if (type === 3) {
        --indicatorCount;
        if (indicatorCount === 0) {
          if (loadingInstance != null) {
            loadingInstance.close();
            loadingInstance = null;
          }
        }
      }

      const res = response.data;
      const result = res.data ? res.data : res.result;

      const code = res.code;
      if (code * 1 !== 0) {
        if (code === "0011") {
          return result;
        }
        if (type > 1 && code !== 11) {
          // 其他错误，提示一下
          Toast(res.message ? res.message : res.userMsg, "error");
        }
        const error = {
          message: res.message ? res.message : res.userMsg
        };
        return Promise.reject(error);
      } else {
        return result;
      }
    },
    error => {
      // console.log("call resp error: " + JSON.stringify(error)); // for debug
      let message = error.message;
      if (error.code === "ECONNABORTED") {
        message = "接口超时";
      }

      if (type > 1) {
        if (error.response.status === 500) {
          Toast("请求失败，服务器错误！", "error");
        } else {
          Toast(message, "error");
        }
      }
      if (type === 3) {
        indicatorCount = 0;
        if (loadingInstance != null) {
          if (error.response.status === 500) {
            loadingInstance.setText("请求失败，服务器错误！");
          } else {
            loadingInstance.setText(message);
          }

          if (isClose) {
            loadingInstance.close();
            loadingInstance = null;
          }
        }
      }

      return Promise.reject(error);
    }
  );

  return service;
}

const call = createService(2, callTimeout);
//接口报错加载不关闭
const ncall = createService(3, callTimeout);
//接口报错加载关闭
const rcall = createService(3, callTimeout, true);

export default {
  get: function(url, params) {
    return call({
      url: url,
      method: "get",
      params: params
    });
  },
  post: function(url, data) {
    return call({
      url: url,
      method: "post",
      data: data
    });
  },
  npost: function(url, data) {
    return ncall({
      url: url,
      method: "post",
      data: data
    });
  },
  rpost: function(url, data) {
    return rcall({
      url: url,
      method: "post",
      data: data
    });
  }
};
