import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/sign",
      name: "index",
      component: () =>
        import(/* webpackChunkName: "Index" */ "./components/Index.vue")
    },
    {
      path: "/preview",
      name: "Preview",
      component: () =>
        import(/* webpackChunkName: "Preview" */ "./components/Preview.vue")
    },
    {
      path: "/success",
      name: "Success",
      component: () =>
        import(/* webpackChunkName: "Preview" */ "./components/Success.vue")
    }
  ]
});
